.button{
	border: 1px solid transparent;
	padding: 0.3rem 0.8rem;
	background-color: #FFC107;
	color: black;
	cursor: pointer;
	border-radius: 5px;
	transition: all .5s;
	box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.4);
	text-decoration: none;
	}
.button:hover{
	border: 1px solid black;
	padding: 0.3rem 0.8rem;
	background-color: #FFC107;
	color: black;
	cursor: pointer;
	border-radius: 5px;
	transition: all .5s;
	box-shadow: 0 0 12px rgba(0, 0, 0, 0.7) 
	}

