.text-container{
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 60%;
    left: 50%;
    -webkit-transform: translate(-50%, -0%);
            transform: translate(-50%, -0%);
    -ms-transform: translate(-50%, -0%);
    color: white;
    width: 100%;
    font-weight: 600;
    padding: 0 20px 0 20px;
}
.text-btn{
    transition: .5s ease;
    opacity: 1;
    position: absolute;
    top: 85%;
    left: 55%;
    -webkit-transform: translate(-50%, -0%);
            transform: translate(-50%, -0%);
    -ms-transform: translate(-50%, -0%);
    width: 100%;
}
.full-container{
    position: relative;
    width: 100%;
    overflow: hidden;
    transition: all 0.5s;
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.205);
}
.full-container img{
    transition: all 0.5s;
}
.full-container:hover .text-container{
    opacity: 1;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}
.full-container:hover img{
    -webkit-filter: blur(1px);
            filter: blur(1px);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all 0.5s;
}

.full-container .image{
    opacity: 1;
  display: block;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
